import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions} from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import {AuthService} from '../../auth/auth.service'
@Component({
  selector: 'app-topproz-signin',
  templateUrl: './topproz-signin.component.html',
  styleUrls: ['./topproz-signin.component.css']
})
export class TopprozSigninComponent implements OnInit {
  serProSignIn: FormGroup;
  custSignIn: FormGroup;
  customerRole: boolean =false;
  prozRole: boolean = true;
  response: any;
  classActive: any;
  classNotActive: any;
  prozCode: any;
  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService:AuthService
  ) {
    this.serProSignIn = this.formBuilder.group({
      Email: ['', Validators.required],
      password: ['', Validators.required],
      role:  ['PROZ']
    });

    this.custSignIn = this.formBuilder.group({
      Email: ['', Validators.required],
      password: ['', Validators.required]
    });

   }

  ngOnInit() {
    this.prozCode = localStorage.getItem("prozCode")
    //console.log("prozCode"+this.prozCode)
    if(this.activatedRoute.snapshot.params['id'] == "CUST"){
      this.classActive = "active"
      this.classNotActive = ""
      this.customerRole = true;
      this.prozRole = false;
      return false;
    } 
    if(this.activatedRoute.snapshot.params['id'] == "PROZ"){
      this.classActive = ""
      this.classNotActive = "active"
      this.customerRole = false;
      this.prozRole = true;
      return false;
    }


  }

  customerTab(){
    this.customerRole = true;
    this.prozRole = false;
  }

  prozTab(){
    this.prozRole = true;
    this.customerRole = false;
  }

  cuSignInBtn(){
    let obj = {
      "emailId":this.custSignIn.value.Email,
      "password":this.custSignIn.value.password
    }
    this.http.post(Global.url+'/custLogin', obj)
    .subscribe(data => {
      var responseData = data.json();
      this.response = responseData.response[0];
      localStorage.setItem('customerLogin', JSON.stringify(this.response))
      //console.log("This is Response: " + JSON.stringify(this.response))
      if(responseData.status === 200){
        this.toastr.success("Logged in Suceessfully...")
        if(this.activatedRoute.snapshot.params['id'] == "PROZ"){
          this.router.navigate(['/customerDashboard'])
        }else{
          this.router.navigate(['/selectProz/', this.response.loginCode])
        }

    }else {
      this.toastr.warning('No Login....');
    } error => {
      //console.log('The execution is failed ' + JSON.stringify(error))
    }
    })
  }

  spSignInBtn(){
    let obj = {
      "emailId":this.serProSignIn.value.Email,
      "password":this.serProSignIn.value.password,
      "role":this.serProSignIn.value.role
    }
    this.authService.login(obj)
      .subscribe(data => {
        var loginData = data
        this.response = data.data
        localStorage.setItem('login', JSON.stringify(this.response))
        if(loginData.status == 200){
          this.router.navigate(['/proz/prozProfile',this.prozCode])
        }
      },error=>{
        this.toastr.warning('Unable to connect to server');
      })    
  }

  newProzRegistrationLink(){
    this.router.navigate(['/newProzRegistration'])
  }
}
