import { Component, ViewChild, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { CustomValidators } from '../../../utilities/customValidators';
import { Input } from '../../../utilities/input';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  providers: [Beyond, CustomValidators, Input],
  selector: 'app-topproz-vanlynk-beapro',
  templateUrl: './topproz-vanlynk-beapro.component.html',
  styleUrls: ['./topproz-vanlynk-beapro.component.scss']
})
export class TopprozVanlynkBeaproComponent implements OnInit {
  registrationFormGroup: FormGroup;
  addTradeLicenseList: FormGroup;
  randamvalue: any;
  public error: boolean = false;
  public mailError: boolean = false;
  disableSignIn: boolean = true;
  yelpExists: boolean = false;
  yelpNotVerified: boolean = false;
  role: any = "VANLYNK"
  leadGValue: any;
  vanlynkValue: any;
  imagePreview: string = "../../assets/img/no-image.png";
  file2Upload: any;
  yearsData: any;
  categoriesData: any;
  id: any;
  roleCode: any;
  leadG: any;
  vanlynk: any;
  tradeArray: any = [];
  items: FormArray;
  percentage: any = 0;
  @ViewChild('imageUpload')
  imageUploadVariable: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  editProfile: boolean = false;
  img: boolean = false;
  public statesData = []
  tradeStatusCheck: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private input: Input,
    private toastr: ToastrService,
    private http: Http,
    private spinner: NgxSpinnerService,
  ) {
    this.registrationFormGroup = this.formBuilder.group({
      companyName: ['', Validators.required],
      // businessType: [''],
      mobileNumber: new FormControl("", Validators.compose([CustomValidators.mobileNumValidator])),
      address: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      zipCode: ['', Validators.required],
      firstname: new FormControl("", Validators.compose([CustomValidators.textValidator])),
      lastname: new FormControl("", Validators.compose([CustomValidators.textValidator])),
      emailId: new FormControl("", Validators.compose([CustomValidators.emailValidator])),
      contactNumber: new FormControl("", Validators.compose([CustomValidators.mobileNumValidator])),
      noOfEmployees: ['', Validators.required],
      establishedYear: ['', Validators.required],
      priceBook:  new FormControl('no'),
      inventorySystem:  new FormControl('no'),
      typeOfSoftware: ['', Validators.required],
      licensesRequired: ['', Validators.required],
      image: [''],
      tradeLicenseState: ['', Validators.required],
      tradeLicenseCategory: ['', Validators.required],
      tradeLicenseNumber: ['', Validators.required],
      tradeLicenseExpiry: ['', Validators.required],
      creditCardType: [''],
      creditCardNumber: [''],
      ccvNumber: [''],
      cardExpiry: [''],
      billingAddress: [''],
      billingState: [''],
      billingCity: [''],
      billingZipCode: [''],
    });

    this.addTradeLicenseList = this.formBuilder.group({
      tradeLicense: this.formBuilder.array([this.createTradeLicense()])
    })

  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.input.phoneno();
    this.getListOfStates();
    this.getYears();
    this.getListOfCategories();
    this.checkValidationTrade();
  }

  //TRADE LICENSE GENERATION
  createTradeLicense() {
    return this.formBuilder.group({
      stateRegisteredIn: ['', Validators.required],
      tradeLicense: ['', Validators.required],
      tradeLicenseNumber: ['', Validators.required],
      tradeLicenseExpiration: ['', Validators.required]
    });
  }

  addFieldValue() {
    this.items = this.addTradeLicenseList.get('tradeLicense') as FormArray;
    this.items.push(this.createTradeLicense());
    this.checkValidationTrade()
  }

  get tradeLicenseValues(): FormArray {
    return this.addTradeLicenseList.get('tradeLicense') as FormArray;
  }

  deleteFieldValue(index: number) {
    this.tradeLicenseValues.removeAt(index);
    this.checkValidationTrade()
  }
  //TRADE LICENSE GENERATION

  //IMAGE UPLOAD
  handleImage(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreview = reader.result;
    }
    picReader.readAsDataURL(file)
  }

  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.img = true;
      this.progressShow = false;
      this.progressHide = false;
      this.file2Upload = event.target.files[0];
      this.handleImage(file)
    } else {
      this.img = false;
      this.progressShow = false;
      this.progressHide = false;
      this.imagePreview = '../../assets/img/no-image.png'
    }
  }

  imgfileUpload() {
    const url = Global.url + '/imageUpload'
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('image', this.file2Upload)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage === 100) {
          this.progressShow = false;
          this.progressHide = true;
          this.imageUploadVariable.nativeElement.value = "";
          this.img = false;
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.imagePath = a.imagePath
    };
    xhr.send(formData);
  }
  //IMAGE UPLOAD

  checkValidationTrade() {
    this.tradeStatusCheck = true
    this.addTradeLicenseList.get('tradeLicense').value.forEach((e) => {

      //console.log(e)
      if (e.stateRegisteredIn == "" || e.tradeLicense == "" || e.tradeLicenseExpiration == "" || e.tradeLicenseNumber == "") {
        //console.log('The empty')
        this.tradeStatusCheck = true
        return false;
      } if (e.stateRegisteredIn == null || e.tradeLicense == null || e.tradeLicenseExpiration == null || e.tradeLicenseNumber == null) {
        //console.log('The empty')
        this.tradeStatusCheck = true
        return false;
      } else if (e.stateRegisteredIn !== "" && e.tradeLicense !== "" && e.tradeLicenseExpiration !== "" && e.tradeLicenseNumber !== "") {
        //console.log('The not empty')
        this.tradeStatusCheck = false
        return false;
      }
    })
  }
  //CHECK TRADE VALIDATIONS

  //STATES
  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //STATES  

  //YEARS
  getYears() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getYears', options)
      .subscribe(data => {
        var response = data.json();
        this.yearsData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //YEARS

  //CATEGORIES
  getListOfCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfCategories', options)
      .subscribe(data => {
        var response = data.json();
        this.categoriesData = response.response;
        // this.categoriesData.forEach((e) => {
        //   e.checked = false;
        // })
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //CATEGORIES

  signin() {

    var val = Math.floor(1000 + Math.random() * 9000);
    this.randamvalue = "VANPRO" + "" + val
    
    let vanlynkObj = {
      vanlynkId: this.randamvalue,
      companyName: this.registrationFormGroup.value.companyName,
      mobileNumber: this.registrationFormGroup.value.mobileNumber,
      address: this.registrationFormGroup.value.address,
      state: this.registrationFormGroup.value.state,
      city: this.registrationFormGroup.value.city,
      zipcode: this.registrationFormGroup.value.zipCode,
      firstName: this.registrationFormGroup.value.firstname,
      lastName: this.registrationFormGroup.value.lastname,
      emailId: this.registrationFormGroup.value.emailId,
      contactNumber: this.registrationFormGroup.value.contactNumber,
      noOfEmployees: this.registrationFormGroup.value.noOfEmployees,
      establishedYear: this.registrationFormGroup.value.establishedYear,
      priceBook: this.registrationFormGroup.value.priceBook,
      inventorySystem: this.registrationFormGroup.value.inventorySystem,
      typeOfSoftware: this.registrationFormGroup.value.typeOfSoftware,
      licensesRequired: this.registrationFormGroup.value.licensesRequired,
      image: this.registrationFormGroup.value.image,
      stateRegisteredIn: this.addTradeLicenseList.value.stateRegisteredIn,
      tradeLicense: this.addTradeLicenseList.value.tradeLicense,
      tradeLicenseNumber: this.addTradeLicenseList.value.tradeLicenseNumber,
      tradeLicenseExpiration: this.addTradeLicenseList.value.tradeLicenseExpiration
    }

    //console.log("vanlynkObj" + JSON.stringify(vanlynkObj))
    //console.log("vanlynkObj" + JSON.stringify(vanlynkObj.tradeLicense))
    this.tradeArray = vanlynkObj.tradeLicense
    // this.id = vanlynkObj.vanlynkId
    // this.tradeArray.push(this.id)
    this.http.post(Global.url + '/vanlynkRegistration', vanlynkObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          localStorage.setItem('regValues', JSON.stringify(response))
          let billingObj = {
            vanlynkId: this.randamvalue,
            creditCardType: this.registrationFormGroup.value.creditCardType,
            creditCardNumber: this.registrationFormGroup.value.creditCardNumber,
            ccvNumber: this.registrationFormGroup.value.ccvNumber,
            cardExpiry: this.registrationFormGroup.value.cardExpiry,
            billingAddress: this.registrationFormGroup.value.billingAddress,
            billingState: this.registrationFormGroup.value.billingState,
            billingCity: this.registrationFormGroup.value.billingCity,
            billingZipCode: this.registrationFormGroup.value.billingZipCode,
          }
          this.http.post(Global.url + '/vanlynkBillingDetails', billingObj)
            .subscribe(data => {
              var response = data.json();
              if (response.status == 200) {
                let tradeObj = {
                  tradeLicenseArray: this.tradeArray,
                  vanlynkId: this.randamvalue,
                }
                //console.log("tradeObj"+JSON.stringify(tradeObj))
                this.http.post(Global.url + '/addBatchTradeLicense', tradeObj)
                  .subscribe(data => {
                    var response = data.json();
                    if (response.status == 200) {
                      this.router.navigate(['/topProz/vanlynkBeAProPassword', this.randamvalue])
                    }
                  })
              }
            })
        }
      }, error => {
        this.toastr.error('Failed to Connect Server');
      })
  }

  //TRADE LICENSE EXPIRATION
  tradeLicenseExpiration() {
    //var date = this.serProzDetails.controls.tradeLicenseExpiration.value;
    //  var changeDate = new Date(date)
  }
  //TRADE LICENSE EXPIRATION

  mailIdVerification() {
    if (this.registrationFormGroup.value.emailId == "") {
      this.toastr.warning("Email Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/mailIdVerification/' + this.registrationFormGroup.value.emailId)
        .subscribe(data => {
          var mailResponse = data.json();
          //console.log("mailResponse"+JSON.stringify(mailResponse))
          this.roleCode = mailResponse.response[0].role
          if(this.roleCode == "PRO"){
            this.leadG = "Y"
            this.vanlynk = "Y"
          }else{
            this.leadG = "N"
            this.vanlynk = "Y"
          }
          localStorage.setItem("status", JSON.stringify({
            'leadG' : this.leadG,
            'vanlynk':  this.vanlynk
          }))
          //console.log("mailResponse"+JSON.stringify(this.roleCode))
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

}
