import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global'

@Component({
  providers:[Beyond],
  selector: 'app-topproz-service-sub-category',
  templateUrl: './topproz-service-sub-category.component.html',
  styleUrls: ['./topproz-service-sub-category.component.css']
})
export class TopprozServiceSubCategoryComponent implements OnInit {
  categoryCodeData: any;
  categoryNameData: any;
  serviceSubCategoryData: any;
  subCategoriesData: any;
  dataServiceSubCategory: boolean =false;
  serviceName: any;
  constructor(
    public beyond:Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.categoryCodeData = this.route.snapshot.params['cat_code']
    this.categoryNameData = this.route.snapshot.params['categoryName']
    this.listSubCategories(this.categoryCodeData)
   
  }

  listSubCategories(categoryCodeData){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url +'/subCategoriesDetails/'+categoryCodeData, options)
      .subscribe(data => {
        this.serviceSubCategoryData = data.json();
        if(this.serviceSubCategoryData.status==200){
          this.subCategoriesData = this.serviceSubCategoryData.response;
          ////console.log("service category Data"+JSON.stringify(this.subCategoriesData))
          this.dataServiceSubCategory = true;
        }else{
          this.dataServiceSubCategory = false;
        }  
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  questions(categoryNameData,subcategoryName){
    this.serviceName = categoryNameData +"-"+subcategoryName
    this.router.navigate(['/selectedService',this.serviceName.trim()])
  }

}
