import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'
@Component({
  providers:[Beyond],
  selector: 'app-topproz-services',
  templateUrl: './topproz-services.component.html',
  styleUrls: ['./topproz-services.component.css']
})
export class TopprozServicesComponent implements OnInit {
  serviceCategoryData:any;
  categoriesData: any;
  dataServiceCategory: boolean =false;
  dataServiceSubCategory: boolean =false;
  catSelection: boolean =false;
  catIcons: any;
  serviceSubCategoryData: any;
  subCategoriesData: any;
  expandedArraySubCategory: any = [];
  serviceName: any;

  constructor(
    public beyond:Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    public route: ActivatedRoute
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listServiceCategories();
  }

  listServiceCategories(){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url +'/categories', options)
      .subscribe(data => {
        this.serviceCategoryData = data.json();
        this.categoriesData = this.serviceCategoryData.response;
        for(var i=0; i< this.categoriesData.length; i++){
          this.listSubCategories(this.categoriesData[i])
          this.categoriesData[i].collapsefalse = true;
          this.categoriesData[i].collapsetrue = false;
          if (this.categoriesData[i].icons === '' || this.categoriesData[i].icons === null ) {
            this.categoriesData[i].icons = '../assets/img/no-image.png'
          } else {
            this.categoriesData[i].icons = Global.url +"/" + this.categoriesData[i].icons
          }
        }
        this.dataServiceCategory = true;
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  
}

listSubCategories(categoryCodeData){
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  this.http.get(Global.url +'/subCategoriesDetails/'+categoryCodeData.cat_code, options)
    .subscribe(data => {
      this.serviceSubCategoryData = data.json();
      if(this.serviceSubCategoryData.status==200){
        this.subCategoriesData = this.serviceSubCategoryData.response;
        ////console.log("service category Data"+JSON.stringify(this.subCategoriesData))
        this.dataServiceSubCategory = true;
      }else{
        this.dataServiceSubCategory = false;
      }  
    }, error => {
      this.toastr.warning('Unable to Connect to Server')
    })
}

subCategories(data){
  this.router.navigate(['/topprozservicesubcategory', data.cat_code, data.categoryName])
}

selectedCategoriesTrue(Category){
  this.expandedArraySubCategory.push(Category)
  if (this.expandedArraySubCategory.length == 2) {
    this.expandedArraySubCategory[this.expandedArraySubCategory.length - 2].collapsefalse = true;
    this.expandedArraySubCategory[this.expandedArraySubCategory.length - 2].collapsetrue = false;
    this.expandedArraySubCategory.shift()
  }
  Category.collapsefalse = false
  Category.collapsetrue = true;
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  this.http.get(Global.url +'/categoriesSubCategories/'+Category.cat_code, options)
    .subscribe(data => {
      var subCategories = data.json() 
      if(subCategories.status == 200){
        this.subCategoriesData = subCategories.response;
      }else{
        Category.collapsefalse = true
        Category.collapsetrue = false;
        return false
      }
    }, error => {
      this.toastr.warning('Unable to Connect to Server')
    })  
}

selectedCategoriesFalse(Category){
  Category.collapsetrue = false;
  Category.collapsefalse = true;
}

questions(categoryNameData,subcategoryName){
   this.serviceName = categoryNameData +"-"+subcategoryName
   this.router.navigate(['/customer/selectedService',this.serviceName.trim()])
}

}