import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Beyond } from '../../../utilities/beyond';
import { Global } from '../../../global/global';

@Component({
  providers:[Beyond],
  selector: 'app-topproz-confirmation',
  templateUrl: './topproz-confirmation.component.html',
  styleUrls: ['./topproz-confirmation.component.scss']
})
export class TopprozConfirmationComponent implements OnInit {
  prozCode: any;
  proMailId:string;
  mailStatus:boolean=false;
  constructor(
    public beyond: Beyond,
    public http: Http,
    public router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.prozCode = this.activatedRoute.snapshot.params['prozId']
    this.getProInfo(this.prozCode)
    //console.log("prozcode"+this.prozCode)
  }
getProInfo(id){
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  this.http.get(Global.url + '/getProMail/' + id,options)
    .subscribe(data => {
      var response = data.json();
      if(response.status=200){
        this.proMailId = response.response[0].emailId;
        this.mailStatus=true
      }else{
        this.mailStatus=false
      }
     
      
    }, error => {

    })
}
  signInBtn(){
    this.router.navigate(['/auth/login/0'])    
  }

  homeBtn(){
    this.router.navigate([''])    
  }
}
