import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TopprozLayoutComponent } from '../topProz/topproz-layout/topproz-layout.component'
//import { TopprozHomeComponent } from '../topProz/topproz-home/topproz-home.component';
import { TopprozPricingComponent } from '../topProz/topproz-pricing/topproz-pricing.component';
import { TopprozProfessionalsComponent } from '../topProz/topproz-professionals/topproz-professionals.component';
import { TopprozSelectCategoryDetailsComponent } from '../topProz/topproz-select-category-details/topproz-select-category-details.component';
import { HowitworksComponent } from '../topProz/topproz-services/howitworks/howitworks.component';
import { TopprozServiceSubCategoryComponent } from '../topProz/topproz-services/topproz-service-sub-category/topproz-service-sub-category.component';
import { TopprozServicesComponent } from '../topProz/topproz-services/topproz-services.component';
import { TopprozSigninComponent } from '../topProz/topproz-signin/topproz-signin.component';
import { TopprozVanlynkComponent } from '../topProz/topproz-vanlynk/topproz-vanlynk.component';
import { TopprozBeaproComponent } from '../topProz/topproz-beapro/topproz-beapro.component';
import { TopprozBeaproPasswordComponent } from '../topProz/topproz-beapro-password/topproz-beapro-password.component';
import { TopprozConfirmationComponent } from './topproz-confirmation/topproz-confirmation.component';
import { TopprozVanlynkBeaproComponent } from './topproz-vanlynk-beapro/topproz-vanlynk-beapro.component';
import { TopprozVanlynkBeaproPasswordComponent } from './topproz-vanlynk-beapro-password/topproz-vanlynk-beapro-password.component';

const routes: Routes = [
  { path: '', component: TopprozLayoutComponent,
  children:[

    {
      path: 'pricing',
      component: TopprozPricingComponent 
    },
    {
      path: 'professionals',
      component: TopprozProfessionalsComponent 
    },
    {
      path: 'topprozSelectCategoryDetails/:serviceName',
      component: TopprozSelectCategoryDetailsComponent 
    },
    {
      path: 'howItWorks',
      component: HowitworksComponent 
    },
    {
      path: 'topprozservicesubcategory/:cat_code/:categoryName',
      component: TopprozServiceSubCategoryComponent 
    },
    {
      path: 'services',
      component: TopprozServicesComponent 
    },
    {
      path: 'signin/:id',
      component: TopprozSigninComponent 
    },
    {
      path: 'vanlynk',
      component: TopprozVanlynkComponent 
    },
    {
      path: 'beAPRO',
      component: TopprozBeaproComponent 
    },
    {
      path: 'passwordConfirmation/:prozId',
      component: TopprozBeaproPasswordComponent 
    },
    {
      path: 'topProzConfirmation/:prozId',
      component: TopprozConfirmationComponent 
    },
    {
      path: 'vanlynkBeAPro',
      component: TopprozVanlynkBeaproComponent 
    },
    {
      path: 'vanlynkBeAProPassword/:vanlynkId',
      component: TopprozVanlynkBeaproPasswordComponent 
    }
   
    
  ]
},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TopProzRoutingModule { }
