  /* getting std markup value */

  otpConfirm() {
    if (this.registrationFormGroup.value.mobileCode == "" || this.registrationFormGroup.value.mobileCode == null) {
      this.otpValidate = false;
      this.otpError = false;
      return false;
    } else {
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/prozOTPValidation/' + this.activatedRoute.snapshot.params['prozId'] + '/' + this.registrationFormGroup.value.mobileCode, options)
        .subscribe(data => {
          var otpValidator = data.json()
          //console.log("mobile code formcntrl"+JSON.stringify(otpValidator))
          if (otpValidator.status == 200) {
            this.otpValidate = true;
            this.otpError = false;
          } else {
            this.otpError = true;
            this.otpValidate = false;
          }
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })
    }
  }


  mailOTPConfirm() {
    if (this.registrationFormGroup.value.mailOTP == "" || this.registrationFormGroup.value.mailOTP == null) {
      this.mailOtpValidate = false;
      this.mailOtpError = false;
      return false;
    } else {
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/prozMailOTPValidation/' + this.activatedRoute.snapshot.params['prozId'] + '/' + this.registrationFormGroup.value.mailOTP, options)
        .subscribe(data => {
          var mailOtpValidator = data.json()
          //console.log("Mail code formcntrl"+JSON.stringify(mailOtpValidator))
          if (mailOtpValidator.status == 200) {
            this.mailOtpValidate = true;
            this.mailOtpError = false;
          } else {
            this.mailOtpError = true;
            this.mailOtpValidate = false;
          }
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })
    }
  }

  resendOTP() {
    var regenerateObj = {
      prozId: this.activatedRoute.snapshot.params['prozId'],
      mobileNumber: this.beAProValues.mobileNumber,
      firstName: this.beAProValues.firstName,
      lastName: this.beAProValues.lastName,
    }
    this.http.post(Global.url + '/resendOTP', regenerateObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.mobileCode = response.OTP;
          this.toastr.warning('Regenerated Verification Code Successfully')
          return false;
        }
      })

  }
  keyPress(event: any) {
    const pattern = /[0-9\+\-\ ]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

