import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { CustomValidators } from '../../../utilities/customValidators';
import { Input } from '../../../utilities/input';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  providers: [Beyond, CustomValidators, Input],
  selector: 'app-topproz-beapro',
  templateUrl: './topproz-beapro.component.html',
  styleUrls: ['./topproz-beapro.component.css']
})
export class TopprozBeaproComponent implements OnInit {
  registrationFormGroup: FormGroup;
  randamvalue: any;
  public error: boolean = false;
  public mailError: boolean = false;
  disableSignIn: boolean = true;
  yelpExists: boolean = false;
  yelpNotVerified: boolean = false;
  role: any = "PRO"
  leadGValue: any;
  vanlynkValue: any;
  countyData: any;
  public statesData = []
  public displayZipcodes = []
  nameOfBussiness: boolean = false;
  BusinessType: boolean = false;
  submitted = false;
  validMobileNumber: boolean = false
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private input: Input,
    private toastr: ToastrService,
    private http: Http,
    private spinner: NgxSpinnerService,
  ) {
    this.registrationFormGroup = this.formBuilder.group({
      nameofbusiness: ['', Validators.required],
      businessType: ['', Validators.required],
      firstname: new FormControl("", Validators.compose([CustomValidators.textValidator, Validators.required])),
      lastname: new FormControl("", Validators.compose([CustomValidators.textValidator, Validators.required])),
      emailID: new FormControl("", Validators.compose([CustomValidators.emailValidator, Validators.required])),
      mobileNumber: new FormControl("", Validators.compose([CustomValidators.mobileNumValidator, Validators.required])),
      smsMobile: new FormControl("", Validators.compose([CustomValidators.mobileNumValidator, Validators.required])),
      businessAddressLine1: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      zipCode: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.input.phoneno();
    this.getListOfStates()
  }
  get f() { return this.registrationFormGroup.controls; }

  async onSubmit() {
    this.submitted = true;
    this.spinner.show()
    // stop here if form is invalid
    if (this.registrationFormGroup.invalid) {
      this.spinner.hide()
      return;
    }
    if (this.mailError == true) {
      this.spinner.hide()
      this.toastr.warning('Please use different EmailId');
      return;
    }
    this.nextBtn()
    // var validateMobileNumberVariable = await this.validateMobileNumber()
    // if(validateMobileNumberVariable['response']['valid'] == true){
    //   this.validMobileNumber = false
    // }else if(validateMobileNumberVariable['response']['valid'] == false){
    //   this.validMobileNumber = true
    //   this.spinner.hide()
    // }
  }


  async validateMobileNumber() {
    return new Promise((resolve, reject) => {
      this.http.get(Global.url + '/mobileNumberValidator/' + this.registrationFormGroup.value.mobileNumber.replace(/\s/g, '') + '/' + 'US')
        .subscribe(data => {
          var response = data.json()
          if (response.status == 200) {
            resolve(response)
          } else {
            resolve(response)
          }
        })
    })
  }




  nextBtn() {

    if (this.registrationFormGroup.value.leads == true) {
      this.leadGValue = "Enable";
    } else {
      this.leadGValue = "Disable";
    }

    if (this.registrationFormGroup.value.vanlynk == true) {
      this.vanlynkValue = "Enable";
    } else {
      this.vanlynkValue = "Disable";
    }
    var val = Math.floor(1000 + Math.random() * 9000);
    this.randamvalue = "PRO" + "" + val
    let newProzObj = {
      prozId: this.randamvalue,
      typeOfBusiness: this.registrationFormGroup.value.nameofbusiness,
      businessType: this.registrationFormGroup.value.businessType,
      firstName: this.registrationFormGroup.value.firstname,
      lastName: this.registrationFormGroup.value.lastname,
      mobileNumber: this.registrationFormGroup.value.mobileNumber,
      smsMobile: this.registrationFormGroup.value.smsMobile,
      emailId: this.registrationFormGroup.value.emailID,
      businessAddress: this.registrationFormGroup.value.businessAddressLine1,
      zipcode: this.registrationFormGroup.value.zipCode,
      city: this.registrationFormGroup.value.city,
      state: this.registrationFormGroup.value.state,
      leadG: this.leadGValue,
      vanlynk: this.vanlynkValue
    }
    //console.log("newProzObj"+JSON.stringify(newProzObj))
    this.http.post(Global.url + '/newProzRegistration', newProzObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          localStorage.setItem('regValues', JSON.stringify(response))
          console.log(newProzObj)
          this.getcountyDetails(newProzObj.city, newProzObj.zipcode, newProzObj.prozId)

        }
      }, error => {
        this.toastr.error('Failed to Connect Server');
      })
  }

  mailIdVerification() {
    if (this.registrationFormGroup.value.emailID == "") {
      // this.toastr.warning("Email Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/newCustomerMailIdVerification/' + this.registrationFormGroup.value.emailID + "/" + this.role)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

  signin() {
    this.router.navigate(['/auth/login/0'])
  }

  //BUSINESS API SOCIAL MEDIA
  changeBusinessName() {
    if (this.registrationFormGroup.controls.nameofbusiness.value != '' && this.registrationFormGroup.controls.businessAddressLine1.value != '' && this.registrationFormGroup.controls.city.value != '' && this.registrationFormGroup.controls.state.value != '' && this.registrationFormGroup.controls.zipCode.value != '') {
      //COMMON OBJECT FOR FETCHING SOCIAL MEDIA RATING
      var businessObj = {
        name: this.registrationFormGroup.controls.nameofbusiness.value,
        address1: this.registrationFormGroup.controls.businessAddressLine1.value,
        city: this.registrationFormGroup.controls.city.value,
        country: 'US',
        state: this.registrationFormGroup.controls.state.value,
        zipcode: this.registrationFormGroup.controls.zipCode.value,
      }
      //COMMON OBJECT FOR FETCHING SOCIAL MEDIA RATING
      //YELP DETAILS FETCHER
      this.http.post(Global.url + '/getYelpBusiness', businessObj)
        .subscribe(data => {
          var response = data.json()
          //console.log('This is yelp verified ' + JSON.stringify(response))
          if (response.businesses == "") {
            this.yelpExists = false
            this.yelpNotVerified = true
            return false;
          } else {
            this.yelpExists = true
            this.yelpNotVerified = false
          }
        }, error => {
          this.toastr.warning('Unable to connect to server');
        })
      //YELP DETAILS FETCHER
    }
  }
  //BUSINESS API SOCIAL MEDIA  
  //STATES
  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //STATES  

  keyPress(event: any) {
    const pattern = /[0-9\+\-\ ]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  keyPress1(event: any) {
    const pattern = /^[a-zA-Z ]+$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && event.keyCode != 9 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  //DEFAULTCOUNTY INSERTION
  //INSERT COUNTIES ZIPCODE BATCH DIRECT
  getcountyDetails(city, zipcode, proId) {
    console.log(city + '-' + zipcode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStateCodewithZipcode/' + city + '/' + zipcode, options)
      .subscribe(data => {
        var response = data.json();
        this.countyData = response.response[0];
        this.getZipcodesFromCountyData(this.countyData.county, this.countyData.stateCode, proId)
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  getZipcodesFromCountyData(data, stateCode, proId) {
    console.log(data)
    this.displayZipcodes = []
    var householdsZipcodes = 0
    var county = data
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getZipcodeFromCounty/' + county + '/' + stateCode, options)
      .subscribe(data1 => {
        var response = data1.json();
        if (response.status == 200) {
          this.displayZipcodes = response.response;
          this.displayZipcodes.forEach((e, i) => {
            e.proCode = proId
            e.zipcode = e.zipCode
            e.checked = false
            householdsZipcodes = householdsZipcodes + parseInt(e.households)
            if (i == this.displayZipcodes.length - 1) {
              this.insertCountiesZipcodeBatchDirect(data, stateCode, this.displayZipcodes, proId)
            }
          })

          return false;
        } else {
          this.displayZipcodes = []
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  insertCountiesZipcodeBatchDirect(county, stateCode, zipcodesData, proId) {
    //console.log(this.activatedRoute.snapshot.params['prozId'])
    console.log(county)
    console.log(zipcodesData)
    console.log(this.displayZipcodes)
    var countiesZipcodeObj = {
      prozId: proId,
      county: county,
      stateCode: stateCode,
      zipcodes: zipcodesData
    }
    this.http.post(Global.url + '/countiesZipcodeBatchInsertDirect', countiesZipcodeObj)
      .subscribe(data => {
        var response = data.json();
        this.spinner.hide()
        this.router.navigate(['/topProz/passwordConfirmation', this.randamvalue])
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //INSERT COUNTIES ZIPCODE BATCH DIRECT
  //DEFAULTCOUNTY INSERTION
}
